


















import { defineComponent, ref, Ref } from '@vue/composition-api';
import { CircleSpinner, CrossIcon } from '@nimiq/vue-components';
import { LocaleMessage } from 'vue-i18n';
import { MAINNET_ORIGIN, TESTNET_ORIGIN } from '../lib/Constants';

type FaucetInfoResponse = {
    network: 'test' | 'main',
    address: string,
    balance: number,
    dispenseAmount: number, // NIM
    dispensesRemaining: number,
    availableInRegion: boolean,
};

type FaucetTapResponse = {
    success: false,
    msg: string,
    error?: 'VAPTCHA_UNAVAILABLE'
        | 'INVALID_CAPTCHA'
        | 'INVALID_ADDRESS'
        | 'GEOBLOCKED'
        | 'OUT_OF_FUNDS'
        | 'TRANSACTION_FAILED',
} | {
    success: false,
    msg: string,
    error: 'RATE_LIMIT',
    wait: number,
} | {
    success: true,
    msg: string,
    expectedBlocks: number,
};

export default defineComponent({
    props: {
        address: {
            type: String,
            required: true,
        },
    },
    setup(props, context) {
        const canTap = ref(true); // Expect the faucet to be available
        const unavailableMsg: Ref<LocaleMessage> = ref('');
        const errorMsg: Ref<LocaleMessage> = ref('');
        const loading = ref(false);

        // Only the testnet faucet is supported, because this component does not implement RECAPTCHA/VAPTCHA
        const FAUCET_URL = 'https://faucet.nimiq-testnet.com';

        const faucetInfoPromise = fetch(`${FAUCET_URL}/info`)
            .then((res) => res.json() as Promise<FaucetInfoResponse>)
            .then((faucet) => {
                const expectedNetwork = {
                    [MAINNET_ORIGIN]: 'main',
                    [TESTNET_ORIGIN]: 'test',
                }[window.location.origin];

                if (faucet.network !== expectedNetwork) {
                    unavailableMsg.value = context.root.$t('Faucet unavailable (wrong network)');
                    canTap.value = false;
                }
                if (faucet.dispensesRemaining < 1) {
                    unavailableMsg.value = context.root.$t('Faucet is empty');
                    canTap.value = false;
                }
                if (!faucet.availableInRegion) {
                    unavailableMsg.value = context.root.$t('Faucet is not available from your location');
                    canTap.value = false;
                }

                return faucet;
            }).catch((error: Error) => {
                console.error(error); // eslint-disable-line no-console
                unavailableMsg.value = `${context.root.$t('Faucet unavailable')} (${error.message})`;
                canTap.value = false;
                return null;
            });

        async function tap() {
            loading.value = true;

            await faucetInfoPromise;
            if (!canTap.value) return false;

            return fetch(`${FAUCET_URL}/tapit`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    address: props.address,
                }),
            }).then((res) => res.json() as Promise<FaucetTapResponse>).then((result) => {
                if (result.success) {
                    // Do nothing and wait until the transaction appears in the list and this component gets replaced.
                    return true;
                }

                loading.value = false;
                errorMsg.value = '';
                unavailableMsg.value = '';

                switch (result.error) {
                    case 'RATE_LIMIT':
                        errorMsg.value = context.root.$t(
                            'You can receive more free NIM in {waitTime} hours.',
                            { waitTime: Math.ceil(result.wait / 3600) },
                        );
                        break;
                    case 'GEOBLOCKED':
                        errorMsg.value = context.root.$t(
                            'This service is currently not available in your region.',
                        );
                        break;
                    case 'OUT_OF_FUNDS':
                        errorMsg.value = context.root.$t('There are currently no free NIM available.');
                        break;
                    case 'TRANSACTION_FAILED':
                        // Set unavailableMsg instead of errorMsg to keep button active for user to try again
                        unavailableMsg.value = context.root.$t('Faucet error - please try again.');
                        break;
                    default:
                        // 'INVALID_CAPTCHA', 'VAPTCHA_UNAVAILABLE', 'INVALID_ADDRESS' or unspecified errors should
                        // not occur via this frontend, therefore no need to translate them.
                        errorMsg.value = `${context.root.$t('Request failed')}: ${result.msg}`;
                }

                return false;
            }).catch((error: Error) => {
                console.error(error); // eslint-disable-line no-console
                errorMsg.value = `${context.root.$t('Request failed')}: ${error.message}`;
                return false;
            });
        }

        return {
            canTap,
            unavailableMsg,
            errorMsg,
            loading,
            tap,
        };
    },
    components: {
        CircleSpinner,
        CrossIcon,
    },
});
